// reducer     
//   .
// dispatch   -> action
//      state 
const reducer = (state,action) => {
    switch(action.type){
        // GET
        case "GET_COMMENT": //  .
            console.log('GET_COMMENT')
            return {
                ...state,
                loadding:true,
            }
        case "GET_COMMENT_SUCCESS": //  .
            console.log('GET_COMMENT_SUCCESS')
            return {
                ...state,
                loadding:false,
                commentItem:action.payload,
            }
        case "GET_COMMENT_ERROR": //   .
            return {
                ...state,
                loadding:false,
                error:action.payload
            }

        // POST
        case "POST_COMMENT":
            return state;
        case "POST_COMMENT_SUCCESS":
            return{
                ...state,
                commentIteam:[...state.commentItem,action.payload]
            }
        case "POST_COMMENT_ERROR":
            return state;
        case "CREATE":
            return {
                ...state,
                commentItem:[...state.commentItem,action.payload]
            }
            
        case "UPDATE":
            console.log(' ? : ',action)
            // let content = action.payload.content;
            // let index = action.payload.index;
            // let {content,index} = {...action.payload}
            // // let {payload:{content,index}} = action
            // let {commentItem} = {...state}
            // commentItem[index].content = content
            // return {
            //     ...state,
            //     commentItem:[...commentItem]
            // }

            let content = action.payload.content;
            let index = action.payload.index;
            let commentItem = state.commentItem //  . point
            console.log(commentItem)
            console.log(commentItem[index]) // index   
            console.log(commentItem[index].content) //  content
            console.log(content) //   content
            commentItem[index].content = content
            console.log('test ',{...state,commentItem})
            return {
                ...state,
            }


            //return state
        case "DELETE":
            console.log(' ? : ',action.payload);
            console.log(state.commentItem)
            let newArr = []
            for(let i=0; i<state.commentItem.length; i++){
                if(i !== action.payload) newArr.push(state.commentItem[i])
            }
            console.log(newArr,state.commentItem)
            // newArr =     
            // state.commentITtem =    
            // (3)[{}, {}, {}] (4)[{}, {}, {}, {}]
            return {
                ...state,
                commentItem:[newArr]
                // commentItem:[...state.commentItem.filter( (v,k)=> action.payload !==k ) ]
            }
        default:
            return state
    }
}

export default reducer 